import { Ref, Computed } from '@linkorgs/reactivity'

export function isString(o: any): o is string {
  return typeof o === 'string'
}

export function isNumber(o: any): o is number {
  return typeof o === 'number'
}

export function isArray(o: any): o is any[] {
  return Array.isArray(o)
}

export function isObject(o: any): o is Record<any, any> {
  return o && typeof o === 'object'
}

const toString = Object.prototype.toString
export function isPlanObject(obj: any): obj is object {
  return toString.call(obj) === '[object Object]'
}

export function isFunction(o: any): o is Function {
  return o && typeof o === 'function'
}

export function haveValue(o: any) {
  return typeof o !== 'undefined' && o !== null
}

export function isRef(n: any): n is Ref {
  return isObject(n) && n._is_ref
}

export function isReactive(n: any): n is Ref | Computed {
  return isObject(n) && Boolean(n._is_ref || n._is_computed || n._is_reactive)
}

export function isComponent(n: any): n is Ref {
  return isObject(n) && n._is_ref
}

const hasOwnProperty = Object.prototype.hasOwnProperty
export function hasOwn(val: object, key: string | symbol): key is keyof typeof val {
  return hasOwnProperty.call(val, key)
}

export function isIntegerKey(key: unknown) {
  return isString(key) && key !== 'NaN' && key[0] !== '-' && '' + parseInt(key, 10) === key
}

export function hasChanged(value: any, oldValue: any): boolean {
  return !Object.is(value, oldValue)
}

export function defineWritable(obj: object, k: any, readonly?: boolean) {
  Object.defineProperty(obj, k, {
    configurable: true,
    writable: !readonly,
  })
}

export function objectReadonlyReducer(obj: object, readonly?: boolean) {
  for (const k in obj) {
    if (hasOwn(obj, k)) {
      const { configurable } = Object.getOwnPropertyDescriptor(obj, k)
      if (configurable) {
        const value = obj[k]
        defineWritable(obj, k, readonly)
        if (isPlanObject(value)) {
          objectReadonlyReducer(value, readonly)
        }
      }
    }
  }
}

export function readonly(obj: object) {
  objectReadonlyReducer(obj, true)
}

export function readwrite(obj: object) {
  objectReadonlyReducer(obj, false)
}

export function isReadonly(obj: object, k: any) {
  const descriptor = Object.getOwnPropertyDescriptor(obj, k)
  if (descriptor) {
    return !descriptor.writable
  }
  return false
}
