import { ref } from '@linkorgs/runtime'
import { useSelector, useDispatch } from '@linkorgs/dox'
import { increment, toggle } from '../store/module/demo'

function TestCount() {
  const state = useSelector()
  const error = ref(false)
  const handleErrorClick = () => {
    try {
      state.Demo.count++
    } catch (_) {
      error.value = true
    }
  }
  return () => (
    <span onClick={handleErrorClick}>
      ({state.Demo.count})
      {error.value && (
        <span
          className="error"
          onClick={(e: Event) => {
            e.stopPropagation()
            error.value = false
          }}
        >
          不允许直接修改 dox 中的数据，请使用 redux dispatch
        </span>
      )}
    </span>
  )
}

export default function Demo() {
  const state = useSelector((state) => state.Demo)
  const dispatch = useDispatch()
  return () => (
    <div className="block text-left">
      <h6>
        下面是使用 <em className="tag">Do</em> 这个框架制作的一些 Demo，你可以在 redux devtools
        看到状态变化并进行时光旅行。想进行更多的尝试可以 clone 本项目后按照 README
        文档进行本地调试。
      </h6>
      <ul>
        <li>
          <div className="flex items-center">
            这是一个累加器按钮
            <TestCount />
            <button
              style={{ margin: '4px' }}
              onClick={() => {
                dispatch(increment())
              }}
            >
              {state.count}
            </button>
          </div>
        </li>
        <li>
          <div className="flex items-center">
            <label>
              这是一个 checkbox 选择框 （{state.checked ? '开' : '关'}）
              <input
                type="checkbox"
                onChange={() => {
                  dispatch(toggle())
                }}
                checked={state.checked}
              />
            </label>
          </div>
        </li>
      </ul>
    </div>
  )
}
