import { defineComponent } from '@linkorgs/runtime'

export default defineComponent(function () {
  const urls = [
    {
      name: 'Do',
      url: 'https://github.com/Linkontoask/do',
    },
    {
      name: 'Link',
      url: 'https://github.com/Linkontoask',
    },
    {
      name: 'Redux DevTools',
      url: 'https://chrome.google.com/webstore/detail/redux-devtools/lmhkpmbekcpmknklioeibfkpmmfibljd',
    },
  ]
  return (props) => (
    <div className="block">
      {props.children}
      <ul>
        {urls.map((item) => (
          <li>
            ({item.name})
            <a href={item.url} style={{ marginLeft: '10px' }}>
              {item.url}
            </a>
          </li>
        ))}
      </ul>
    </div>
  )
})
