import { createApp } from '@linkorgs/runtime'
import { Provider } from '@linkorgs/dox'
import { store } from './store'
import App from './app'

void (function run() {
  const container = document.querySelector('#root') as HTMLDivElement
  createApp(
    <Provider store={store}>
      <App />
    </Provider>
  ).mount(container)
})()
