import {
  configureStore,
  ConfigureStoreOptions,
  Middleware,
  AnyAction,
  Action,
} from '@reduxjs/toolkit'
import { ThunkMiddlewareFor } from '@reduxjs/toolkit/src/getDefaultMiddleware'
import { reactive, pauseTracking, resetTracking } from '@linkorgs/runtime'
import { readonly, readwrite, isFunction } from '@linkorgs/shared'
import { diff } from 'deep-diff'
import cloneDeep from 'lodash/cloneDeep'
import { getValueWithPath, noop } from './utils'

export type Middlewares<S> = ReadonlyArray<Middleware<{}, S>>

export interface Store {
  dispatch: (...args: any[]) => any
  getState: () => any
}

let _store: Store
let _state = reactive<any>({}, true)

export function useDispatch() {
  if (!_store) {
    console.warn(
      'Please use the Provider component to register the store, and then use the hook function provided by dox'
    )
    return noop
  }
  return _store.dispatch
}

export function useSelector(sliceName?: string | ((state: any) => any)) {
  if (isFunction(sliceName)) {
    return sliceName(_state)
  }
  return sliceName ? _state[sliceName] : _state
}

export function useStore() {
  if (!_store) {
    console.warn(
      'Please use the Provider component to register the store, and then use the hook function provided by dox'
    )
    return {
      dispatch: noop,
      getState: noop,
    }
  }
  return _store
}

export function wrapperProvider(store: Store) {
  _store = store
}

const dox: Middleware = function dox({ getState }) {
  return function (next) {
    return function (action) {
      const value = next(action)
      return value
    }
  }
}

export function createStore<
  S = any,
  A extends Action = AnyAction,
  M extends Middlewares<S> = [ThunkMiddlewareFor<S>]
>(options: ConfigureStoreOptions<S, A, M>) {
  const defaultOptions: ConfigureStoreOptions<S, A, M> = Object.assign({}, options, {
    middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(dox),
  })
  const store = configureStore(defaultOptions)
  const state = store.getState()
  _state = reactive(cloneDeep(state || {}), true)

  store.subscribe(() => {
    const state = store.getState()
    const change = diff(_state, state)
    pauseTracking()
    // TODO 待测试性能 可选对应 property writable 的数据描述符
    readwrite(_state)
    change.forEach((v) => {
      const { kind, path } = v
      // TODO 同步 redux 数据到 reactive 以便更新 View
      if (kind === 'E') {
        getValueWithPath(path, _state, (obj, cur) => {
          obj[cur] = v.rhs
        })
      }
      if (kind === 'A') {
        getValueWithPath(path, _state, (obj, cur) => {
          if (v.item.kind === 'N') {
            obj[cur].push(v.item.rhs)
          }
        })
      }
    })
    // TODO 待测试性能
    readonly(_state)
    resetTracking()
  })

  return store
}
