import { isObject, isFunction } from '@linkorgs/shared'
import { Component } from './component'
import { DNode } from './dnode'
import { error } from '../shared/assert'

export function callWithErrorHandling(
  fn: Function,
  instance: Component | null,
  type: any,
  args?: unknown[]
) {
  let res
  try {
    res = args ? fn(...args) : fn()
  } catch (err) {
    error(err, instance, type)
  }
  return res
}

export function normalizeComponent(dnode: DNode, component: Component) {
  const { type } = dnode

  let render
  if (isObject(type) && type.render) {
    render = type.render
  }
  if (isFunction(type)) {
    render = type
  }

  if (dnode.context) {
    component.props = dnode.context.props
  }
  component.dnode = dnode
  component.render = render
}

export function normalizeComponentProps(component: Component) {
  // <A><span></span></A> ===== ({children}) => {}
  // merge children to component function params
  const props = Object.assign({}, component.props?.props || {}, component.props, {
    children: component.dnode.children,
  })
  delete props.props
  delete props.on

  return props as {
    children?: DNode
    [k: string]: any
  }
}
