import { createSlice } from '@linkorgs/dox'

const demo = createSlice({
  name: 'Demo',
  initialState: {
    count: 0,
    checked: false,
  },
  reducers: {
    increment(state) {
      state.count++
    },
    toggle(state) {
      state.checked = !state.checked
    },
  },
})

export const { increment, toggle } = demo.actions

export default demo.reducer
