import { ReactiveEffect } from '@linkorgs/reactivity'
import { isDNode } from '../shared'
import { isFunction } from '@linkorgs/shared'
import { DNode, DNodeContext, DNodeTypes, normalizeDNode } from './dnode'
import {
  callWithErrorHandling,
  normalizeComponent,
  normalizeComponentProps,
} from './renderComponentUtil'
import { SchedulerJob } from './scheduler'

export type Data = Record<string, any>

export type ComponentCtx = Data

export type ComponentDefaultProps = {
  children?: DNode
}

export type WithComponent<Props, R> = (
  props: Props & ComponentDefaultProps,
  ctx?: ComponentCtx
) => R

export type ComponentSetupFunction<Props = Data> = WithComponent<Props, DNode>

export type ComponentFunction<Props extends Data = Data> = WithComponent<
  Props,
  DNode | ComponentSetupFunction<Props>
>

export interface Component {
  uid: number
  mounted: boolean
  parent: Component | null
  subTree: DNode | null
  dnode: DNode
  type: DNodeTypes
  effect: ReactiveEffect
  update: SchedulerJob
  render: ComponentFunction
  setup: ComponentSetupFunction
  props: Data
  ctx: Data
}

let uid = 0
export function createComponentInstance(dnode: DNode, parent: Component | null): Component {
  const component: Component = {
    uid: uid++,
    mounted: false,
    parent: parent,
    subTree: null,
    dnode: null,
    type: dnode.type,
    effect: null,
    update: null,
    render: null,
    setup: null,
    props: null,
    ctx: null,
  }
  normalizeComponent(dnode, component)
  return component
}

export function renderComponent(component: Component): DNode<Component> {
  let { render, setup, mounted } = component
  let dnode: DNode<Component>

  if (mounted) {
    render = setup || render
  } else {
    component.mounted = true
  }

  if (isFunction(render)) {
    const props = normalizeComponentProps(component)
    let setupOrTree = callWithErrorHandling(render, component, 'render', [props, component.ctx]) as
      | DNode
      | ComponentSetupFunction
    if (isFunction(setupOrTree)) {
      component.setup = setupOrTree
      setupOrTree = callWithErrorHandling(setupOrTree, component, 'setup', [
        props,
        component.ctx,
      ]) as DNode
    }
    const subTree = isDNode(setupOrTree) ? setupOrTree : normalizeDNode(setupOrTree)
    subTree.parent = component.dnode
    subTree._root = component.dnode._root
    subTree.context = subTree.context || {}
    subTree.context.props = Object.assign(subTree.context.props || {}, component.props)
    dnode = subTree
  } else {
    dnode = component.dnode
  }

  return dnode
}

export function defineAsyncComponent() {}

export function defineComponent<Props extends Data = {}>(
  componentFunction: ComponentFunction<Props>
) {
  return componentFunction
}
